.error-boundary {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 2rem;
}

.error-content {
  background: white;
  padding: 3rem;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 20px 60px rgba(0,0,0,0.2);
  max-width: 500px;
  width: 100%;
}

.error-content h1 {
  color: #1f2937;
  margin-bottom: 1rem;
  font-size: 2rem;
}

.error-content p {
  color: #6b7280;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.reload-btn {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.reload-btn:hover {
  transform: translateY(-2px);
}

.error-details {
  margin-top: 2rem;
}

.error-details details {
  text-align: left;
}

.error-details summary {
  cursor: pointer;
  color: #667eea;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.error-details p {
  background: #f8fafc;
  padding: 1rem;
  border-radius: 8px;
  font-family: monospace;
  font-size: 0.9rem;
  color: #ef4444;
}
